<template>
  <div class="creation">
    <div>
      <el-button type="primary" size="small" @click="categoryVisible = true">
        
      </el-button>
      <el-button type="primary" size="small" @click="goodsVisible = true">
        
      </el-button>
      <el-button type="primary" size="small" @click="combinationVisible = true">
        
      </el-button>
      <el-button type="primary" size="small" @click="goodsSearchVisible = true">
        
      </el-button>
      <el-button
        type="primary"
        size="small"
        @click="goodsCategoryVisible = true"
      >
        
      </el-button>
    </div>
    <el-dialog title="" :visible.sync="categoryVisible" width="300px">
      <div>
        <el-input v-model="newCategory" placeholder="" clearable />
      </div>
      <span slot="footer">
        <el-button size="small" @click="categoryVisible = false">
          
        </el-button>
        <el-button size="small" type="primary" @click="addCategory">
          
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="goodsVisible"
      width="400px"
      @opened="goodsOpened"
      @closed="goodsClosed"
    >
      <el-form
        :model="goodsForm"
        ref="goodsForm"
        :rules="goodsRules"
        label-width="60px"
        :inline="false"
        size="small"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="goodsForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="category">
          <el-select v-model="goodsForm.category" multiple filterable>
            <el-option
              v-for="item in categoryList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="prop">
          <el-select v-model="goodsForm.prop">
            <el-option
              v-for="item in propList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button size="small" @click="goodsVisible = false">  </el-button>
        <el-button size="small" type="primary" @click="addGoods">
          
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="combinationVisible"
      width="500px"
      @opened="combinationOpened"
      @closed="combinationClosed"
    >
      <el-form
        :model="combinationForm"
        ref="combinationForm"
        :rules="combinationRules"
        label-width="80px"
        :inline="false"
        size="small"
      >
        <el-form-item label="">
          <el-checkbox v-model="onlyShowNullRecord"></el-checkbox>
          <el-checkbox v-model="finallyShow"></el-checkbox>
        </el-form-item>
        <el-form-item label="1" prop="origin1">
          <el-select
            v-model="combinationForm.origin1"
            filterable
            @change="origin1Change"
          >
            <el-option
              v-for="item in finallyShow
                ? goodsList
                : goodsList.filter((el) => el.prop !== '')"
              :key="item.name"
              :label="item.name"
              :value="item.name"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="2" prop="origin2">
          <el-select
            v-model="combinationForm.origin2"
            :disabled="!combinationForm.origin1"
            filterable
            multiple
          >
            <el-option
              v-for="item in originList2"
              :key="item.goods"
              :label="item.goods"
              :value="item.goods"
            >
              <div style="display: flex; justify-content: space-between">
                <span>{{ item.goods }}</span>
                <span
                  :style="{
                    color:
                      item.status === ''
                        ? '#b1b6b1'
                        : item.status === ''
                        ? '#000'
                        : 'red',
                  }"
                  >{{ item.status }}</span
                >
              </div>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="result">
          <el-select v-model="combinationForm.result" filterable>
            <el-option
              v-for="item in [...goodsList].reverse()"
              :key="item.name"
              :label="item.name"
              :value="item.name"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button size="small" @click="combinationVisible = false">
          
        </el-button>
        <el-button size="small" type="primary" @click="addCombination">
          
        </el-button>
      </span>
    </el-dialog>
    <el-drawer
      custom-class="goods-search"
      title=""
      :visible.sync="goodsSearchVisible"
      size="600px"
    >
      <div>
        <el-select
          v-model="goodsSearch"
          filterable
          @change="goodsCombinationSearch"
        >
          <el-option
            v-for="item in goodsList"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
        <el-tabs v-model="goodsSearchTab" type="card" tab-position="top">
          <el-tab-pane label="" name="">
            <div>{{ originCombinationList.length }}</div>
            <ul>
              <li
                v-for="item in originCombinationList"
                :key="`${item.origin.join('+')}=${item.result}`"
              >
                {{ item.origin.join(" + ") }} = {{ item.result }}
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="" name="">
            <div>{{ resultCombinationList.length }}</div>
            <ul>
              <li
                v-for="item in resultCombinationList"
                :key="`${item.origin.join('+')}=${item.result}`"
              >
                {{ item.origin.join(" + ") }} = {{ item.result }}
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="" name="">
            <div>{{ goodsCombinationDetail.total }}</div>
            <div>{{ goodsCombinationDetail.hasCombination }}</div>
            <div>
              
              {{
                goodsCombinationDetail.total
                  ? (
                      (goodsCombinationDetail.hasCombination /
                        goodsCombinationDetail.total) *
                      100
                    ).toFixed(5)
                  : 0
              }}%
            </div>
            <div>{{ goodsCombinationDetail.notCombination }}</div>
            <div>
              
              {{
                goodsCombinationDetail.total
                  ? (
                      (goodsCombinationDetail.notCombination /
                        goodsCombinationDetail.total) *
                      100
                    ).toFixed(5)
                  : 0
              }}%
            </div>
            <div>
              {{
                goodsCombinationDetail.total -
                goodsCombinationDetail.hasCombination -
                goodsCombinationDetail.notCombination
              }}
            </div>
            <div>
              
              {{
                goodsCombinationDetail.total
                  ? (
                      ((goodsCombinationDetail.total -
                        goodsCombinationDetail.hasCombination -
                        goodsCombinationDetail.notCombination) /
                        goodsCombinationDetail.total) *
                      100
                    ).toFixed(5)
                  : 0
              }}%
            </div>
            <div>
              
              {{
                goodsCombinationDetail.total
                  ? (
                      ((goodsCombinationDetail.hasCombination +
                        goodsCombinationDetail.notCombination) /
                        goodsCombinationDetail.total) *
                      100
                    ).toFixed(5)
                  : 0
              }}%
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-drawer>
    <el-drawer
      custom-class="goods-category"
      title=""
      :visible.sync="goodsCategoryVisible"
      size="1200px"
      @open="getGoodsCategory"
    >
      <el-tabs v-model="goodsCategoryTab" tab-position="top">
        <el-tab-pane
          v-for="item in goodsCategory"
          :key="item.name"
          :label="`${item.name}(${item.children.length})`"
          :name="item.name"
        >
          <div>{{ item.children.length }}</div>
          <ul>
            <li v-for="el in item.children" :key="`${item.name}-${el}`">
              {{ el }}
            </li>
          </ul>
        </el-tab-pane>
      </el-tabs>
    </el-drawer>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "Index",
  data() {
    return {
      categoryVisible: false,
      goodsVisible: false,
      combinationVisible: false,
      goodsSearchVisible: false,
      goodsCategoryVisible: false,
      newCategory: "",
      propList: ["", "", ""],
      categoryList: [],
      goodsList: [],
      goodCombinationList: [],
      goodsForm: {
        name: "",
        category: "",
        prop: "",
      },
      goodsRules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        category: [],
        prop: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
      },
      combinationForm: {
        origin1: "",
        origin2: "",
        result: "",
      },
      combinationRules: {
        origin1: [
          {
            required: true,
            message: "1",
            trigger: "change",
          },
        ],
        origin2: [
          {
            required: true,
            message: "2",
            trigger: "change",
          },
        ],
      },
      onlyShowNullRecord: false,
      finallyShow: false,
      goodsSearchTab: "",
      goodsSearch: "",
      originCombinationList: [],
      resultCombinationList: [],
      goodsCombinationDetail: {
        total: 0,
        hasCombination: 0,
        notCombination: 0,
      },
      goodsCategory: {},
      goodsCategoryTab: "",
    };
  },
  computed: {
    originList2() {
      const list = this.onlyShowNullRecord
        ? this.goodCombinationList.filter((el) => el.status === "")
        : this.goodCombinationList;
      const list1 = this.finallyShow
        ? list
        : list.filter((el) => el.prop !== "");
      list1.sort((a, b) => (a.py > b.py ? 1 : -1));
      return list1;
    },
  },
  methods: {
    getCategoryList() {
      axios.get("/api/creation/category").then((res) => {
        this.categoryList = res.data.data || [];
      });
    },
    getGoodsList() {
      axios.get("/api/creation/goods").then((res) => {
        this.goodsList = res.data.data || [];
      });
    },
    addCategory() {
      if (this.newCategory) {
        axios
          .post("/api/creation/category", {
            category: this.newCategory,
          })
          .then((res) => {
            if (res.data.success) {
              this.$message.success("");
              this.getCategoryList();
              this.newCategory = "";
              this.categoryVisible = false;
            } else {
              this.$message.error(res.data.message);
            }
          });
      } else {
        this.$message.error("");
      }
    },
    goodsOpened() {
      this.$refs.goodsForm.clearValidate();
    },
    goodsClosed() {
      this.goodsForm = {
        name: "",
        category: "",
        prop: "",
      };
    },
    combinationOpened() {
      this.$refs.combinationForm.clearValidate();
      if (this.combinationForm.origin1) {
        this.getGoodsCombinationStatus(this.combinationForm.origin1);
      }
    },
    combinationClosed() {
      this.combinationForm = {
        origin1: this.combinationForm.origin1,
        origin2: "",
        result: "",
      };
    },
    addGoods() {
      this.$refs.goodsForm.validate((valid) => {
        if (valid) {
          axios
            .post("/api/creation/goods", {
              name: this.goodsForm.name,
              category: this.goodsForm.category,
              prop: this.goodsForm.prop,
            })
            .then((res) => {
              if (res.data.success) {
                this.$message.success("");
                this.getGoodsList();
                this.goodsVisible = false;
              } else {
                this.$message.error(res.data.message);
              }
            });
        } else {
          this.$message.error("");
        }
      });
    },
    getGoodsCombinationStatus(goods) {
      if (!goods) return;
      axios({
        method: "get",
        url: "/api/creation/goods/combinationStatus",
        params: {
          goods,
        },
      }).then((res) => {
        const data = res.data.data || {};
        const arr = [];
        for (let key in data) {
          arr.push({
            goods: key,
            status: data[key].status,
            py: data[key].py,
          });
        }
        this.goodCombinationList = arr;
      });
    },
    origin1Change(goods) {
      this.getGoodsCombinationStatus(goods);
    },
    addCombination() {
      this.$refs.combinationForm.validate((valid) => {
        if (valid) {
          axios
            .post("/api/creation/combination", {
              origin1: this.combinationForm.origin1,
              origin2: this.combinationForm.origin2,
              result: this.combinationForm.result,
            })
            .then((res) => {
              if (res.data.success) {
                this.$message.success("");
                this.combinationVisible = false;
              } else {
                this.$message.error(res.data.message);
              }
            });
        } else {
          this.$message.error("");
        }
      });
    },
    goodsCombinationSearch() {
      axios
        .get("/api/creation/goods/combination", {
          params: {
            goods: this.goodsSearch,
          },
        })
        .then((res) => {
          const data = res.data.data || {};
          this.originCombinationList = data.originCombinationList || [];
          this.resultCombinationList = data.resultCombinationList || [];
        });
      axios
        .get("/api/creation/goods/combinationStatus", {
          params: {
            goods: this.goodsSearch,
          },
        })
        .then((res) => {
          const data = res.data.data || {};
          const detail = {
            total: 0,
            hasCombination: 0,
            notCombination: 0,
          };
          for (let key in data) {
            detail.total++;
            if (data[key].status === "") {
              detail.notCombination++;
            } else if (data[key].status !== "") {
              detail.hasCombination++;
            }
          }
          this.goodsCombinationDetail = detail;
        });
    },
    getGoodsCategory() {
      axios.get("/api/creation/category/goods").then((res) => {
        const goodsCategory = res.data.data || {};
        this.goodsCategory = Object.keys(goodsCategory).map((key) => ({
          key,
          ...goodsCategory[key],
        }));
        this.goodsCategory.sort(
          (a, b) => b.children.length - a.children.length
        );
        this.goodsCategoryTab = this.goodsCategory[0].name;
      });
    },
  },
  created() {
    this.getCategoryList();
    this.getGoodsList();
  },
};
</script>
<style lang="scss" scoped>
.creation {
  .el-select {
    width: 100%;
  }
  .origin2-option {
    display: flex;
    justify-content: space-between;
  }
  .el-drawer__wrapper {
    /deep/ {
      .el-drawer__header {
        margin-bottom: 20px;
      }
      .el-drawer__body {
        padding: 0 20px;
        .el-select {
          margin-bottom: 10px;
        }
      }
    }
  }
}
</style>
